.cover-login {
	background: url('../images/home/banner.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 100vh;
	i:hover {
		color: #0000ff; }
	h2 {
		font-family: 'Shadows Into Light', cursive;
		font-size: 35px;
		text-align: center;
		color: #fff; }
	& > .container {
		background-color: rgba(0,0,0,0.8);
		height: 100vh;
		width: 100vw; }
	#loginpanel {
		.panel-heading {
			font-size: 15px;
			padding: 5px auto; } } }

.white {
	color: #000;
	background-color: #fff; }
